.sequence-input-container {
    position: relative;
    border: 1px solid #E9EBF7;
    padding: 12px 12px 48px 12px;
    border-radius: 12px;
}
.sequence-textarea {
    height: 100%;
    overflow-x: auto;
    border: none;
    resize: none !important;
}

.sequence-input-toolbar {
    width: 100%;
    position: absolute;
    bottom: 8px;
    left: 0;
    padding: 0 12px;
}

